/* Header  */

.header {
    background-color: #293b5f;
    height: 80px;
    position: relative;
}

.inner {
    display: flex;
    justify-content: left;
    align-items: center;
}

.title {
    position: absolute;
    top: 35%;
    left: 10%;
    color: #fff;
}

.letter {
    color: #1768ac;
}

.authBlock {
    float: right;
    color: #fff;
    padding-top: 25px;
}

.NavLink, .NavLink:hover{   
    color: #fff;
    text-decoration: none;
}